import React, { useState } from 'react'
import { Rating } from './Rating';

export default function Card(props) {

    const [addItemState, setAddItemState] = useState(false);
    const [removeItemState, setRemoveItemState] = useState(true);

    const changeState = () => {
        (addItemState) ? setAddItemState(false) : setAddItemState(true);
        (removeItemState) ? setRemoveItemState(false) : setRemoveItemState(true);
    }

    return (
        <div className="col-sm-6 col-md-4 col-lg-3 my-3">
            <div className="card h-100">
                <img
                    src={props.img}
                    className="card-img-top h-100"
                    alt="Product"
                />
                <div className="card-body">
                    <h5 className="card-title text-center">{props.title}</h5>
                    <p className="card-text text-center">Rs.{props.price}</p>
                    <Rating />
                </div>
                <div className="card-footer d-flex justify-content-between border-0 bg-transparent p-3">
                    <button className="btn btn-primary" disabled={addItemState} onClick={changeState}>Add Item</button>
                    <button className="btn btn-danger" disabled={removeItemState} onClick={changeState}>Remove Item</button>
                </div>
            </div>
        </div>
    )
}

